*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


body{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: #f1f5f8;
    color: #222;
}

.booklist{
    width: 90vw;
    max-width: 1200px;
    margin: 5rem auto;
    display: grid;
    gap: 2rem;
}


@media screen and (min-width:768px){
    .booklist{
        grid-template-columns: repeat(3, 1fr);
        
    }
}



.book{
    background: #fff;
    border: 2px dotted black;
    border-radius: .5rem;
    padding: 1rem 2rem;
   
}

.book h1{
    margin-top: .5rem;
}



.book h4{
    margin-top: .5rem;
}


.title{
    text-align: center;
    margin-top: 1rem;
   color: lightcoral;
    font-weight: bolder;
}

hr{
    width: 8rem;
    border: 2px solid firebrick;
   margin: .5rem auto;
}